<template>
  <div>
    <el-row :gutter="10" v-if="video.url">
      <el-col class="mb-12" :xs="24">
        <h1 class="video-title">{{ video.title }}</h1>
        <PlayerVideo :video="video" class="m-b-8" />

        <p class="flex-wrap d-flex">
          <el-tag
            size="medium"
            v-for="(item, idx) in video.tag"
            @click="open(item)"
            :key="idx"
            >{{ item }}</el-tag
          >
        </p>
      </el-col>
    </el-row>
    <LayoutList :docs="video.docs" />
  </div>
</template>

<script>
import apiUser from "@/api/user";
import auth from "@/plugins/auth";
export default {
  name: "DetailView",
  data() {
    return {
      id: this.$route.params.id,
      path: this.$route.path,
      video: {},
    };
  },
  methods: {
    open(item) {
      window.location.href = `/cat/${item}`;
    },
    async queryApplyInfo() {
      const [err, res] = await apiUser.queryApplyInfo(this.path);
      if (err) return;
      this.video = {
        ...res.video,
        url: `${res.video.url}`,
      };
      let view = auth.getToken("view");
      view = view ? JSON.parse(view) : [];
      if (!view.includes(this.video._id)) {
        view.push(this.video._id);
        auth.setToken("view", JSON.stringify(view));
      }
    },
  },
  async created() {
    await this.queryApplyInfo();
    // const test = { node: 1 };
    // const data = crypto.aesCBCEncrypt(JSON.stringify(test));
    // console.log("aesCBCEncrypt", data);
    // console.log("aesCBCDecrypt", crypto.aesCBCDecrypt(data));
    // const [, res] = await apiUser.AddressList();
    // console.log(res);
  },
};
</script>
